// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
// import axios from 'axios'
// 引入请求数据的对象
// import axios from '@/js/axios.js'
// 引入开标自定义js
import eokb from '@/js/eokb.js'
// 引入开标自定义js
import request from '@/js/request.js'
// 引入开标自定义js
import session from '@/js/session.js'
// 引入开标自定义js
import im from '@/js/im.js'
// 引入开标自定义js
import command from '@/js/command.js'

Vue.config.productionTip = false

// 那么在其他vue组件中就可以this.$axios调用使用
// Vue.prototype.$axios = axios
// 那么在其他vue组件中就可以this.$eokb调用使用
Vue.prototype.$eokb = eokb
// 那么在其他vue组件中就可以this.$request调用使用
Vue.prototype.$request = request
// 那么在其他vue组件中就可以this.$session调用使用
Vue.prototype.$session = session
// 那么在其他vue组件中就可以this.$im调用使用
Vue.prototype.$im = im
// 那么在其他vue组件中就可以this.$command调用使用
Vue.prototype.$command = command

//这一句为了解决cordova加载顺序问题
document.addEventListener('deviceready', function () {
  /* eslint-disable no-new */
  new Vue({
    el: '#app',
    router,
    components: { App},
    template: '<App/>'
  })
})



