// @ == src
import eokb from '@/js/eokb.js'

const defaultOptions = {
  // 请求方法
  method: 'post',
  // timeout value for the request in seconds, defaults to global timeout value
  timeout: 10
}
const request = {

  /**
   * 发送请求
   * @param {请求的相对URL} url 
   * @param {请求的参数,详见https://github.com/silkimen/cordova-plugin-advanced-http#readme} options 
   * @param {成功的回调函数} success 
   * @param {失败的回调函数} doFail 
   */
  sendRequest(url, options, success, doFail) {
    if (null == options || typeof (options) == 'undefined') {
      options = {}
    }
    let dfopts = defaultOptions
    // 请求头,这个参数一定要每次都重新设置,不能定义成常量,否则永久不变
    dfopts.headers = {
      // 请求认证的TOKEN
      'Authorization': eokb.getHttpRequestAuthzToken(),
      // 招标项目ID
      'EOKB-TPID': eokb.getTenderProjectID(),
      // 当前用户类型
      'EOKB-USER-TYPE': eokb.getUserType(),
      // 设置客户端类型
      'EOKB-CLIENT-TYPE': eokb.getClientType()
    }
    let opts = Object.assign(dfopts, options)
    // alert(JSON.stringify(opts))
    // if (opts.params!=null){
    //   opts.data = opts.params
    // }
    let fullURL = eokb.getRegionRoomURL() + url
    okapp.requestExt(fullURL, opts, function (response) {
      console.log('request结果:' + response)
      var resultJson = {}
      try {
        resultJson = JSON.parse(response.data)
      } catch(e) {
        resultJson = response.data
      // console.error('JSON parsing error')
      } finally {
        success(resultJson, response)
      }
    }, function (response) {
      var errorJson = {}
      try {
        errorJson = JSON.parse(response.error)
      } catch(e) {
        console.log(JSON.stringify(response))
        // 如果是连接不到服务器
        if (-1 == response.status) {
          errorJson = {errorCode: '-1',errorDesc: '无法连接到远程服务器,请稍后再试!'}
        }else if (1 == response.status) {
          errorJson = {errorCode: '1',errorDesc: '连接超时,请稍后再试!'}
        }else {
          errorJson = {errorCode: '0',errorDesc: '请求发生异常,请稍后再试!'}
        }
      } finally {
        // Token 失效
        if ('EOKB-TOKEN-0001' == errorJson.errorCode) {
          okapp.alert({message: 'Token刷新,请重试!'})
          return
        }
        // 如果不存在错误处理函数
        if (null == doFail || typeof (doFail) == 'undefined') {
          okapp.alert({message: errorJson.errorDesc})
          return
        }
        doFail(errorJson, response)
      }
    })
  },

  /**
   * 发送get请求
   * @param {请求相对URL} url 
   * @param {成功回调} success 
   */
  doGet(url, success) {
    let opts = {method: 'get'}
    this.sendRequest(url, opts, success, null)
  }

}

// 导出对象，然后在全局注册或在单个vue文件下调用
export default request



