// @ == src
import eokb from "@/js/eokb.js";
/**
 * 这个JS专门用来处理即时通讯的各种命令解析
 */
const command = {
  // Main.vue
  MAIN_PAGE: 0,
  // IM.vue
  IM_PAGE: 1,
  // Message.vue
  MESSAGE_PAGE: 2,
  // Participant.vue
  PARTICIPANT_PAGE: 3,
  // Notice.vue
  NOTICE_PAGE: 4,
  // PorjectFLow.vue
  FLOW_PAGE: 5,
  // 其他非流程页面
  OTHER_PAGE: 6,

  /**
   * Main.vue
   */
  mainPage: {
    /**
     * 空函数纯粹为了应付报错
     */
    hintOnline() {}
  },

  /**
   * IM.vue
   */
  imPage: null,

  /**
   * Message.vue
   */
  messagePage: {
    /**
     * 空函数纯粹为了应付报错
     */
    getMeetingRoomTenderer() {}
  },

  /**
   * Participant.vue
   */
  participantPage: {
    /**
     * 空函数纯粹为了应付报错
     */
    getParticipants() {},

    /**
     * 空函数纯粹为了应付报错
     */
    hintOnline() {}
  },

  /**
   * Notice.vue
   */
  noticePage: {
    /**
     * 空函数纯粹为了应付报错
     */
    getTPNotices() {}
  },

  /**
   * 当前页
   */
  currentPage: -1,

  /**
   * 消息列表
   */
  msgs: [],

  /**
   * 公告数量
   */
  noticeCount: 0,

  /**
   * 设置Main.vue
   * @param {Main} p
   */
  setMainPage(p) {
    this.mainPage = p;
  },
  /**
   * 设置IM.vue
   * @param {IM} p
   */
  setIMPage(p) {
    this.imPage = p;
  },
  /**
   * 设置Message.vue
   * @param {Message} p
   */
  setMessagePage(p) {
    this.messagePage = p;
  },
  /**
   * 设置Participant.vue
   * @param {Participant} p
   */
  setParticipantPage(p) {
    this.participantPage = p;
  },
  /**
   * 设置Notice.vue
   * @param {Notice} p
   */
  setNoticePage(p) {
    this.noticePage = p;
  },
  /**
   * 设置PorjectFLow.vue
   * @param {PorjectFLow} p
   */
  setPorjectFLow(p) {
    this.PorjectFLow = p;
  },

  /**
   * 当前页
   * @param {当前页名称} n
   */
  setCurrentPage(n) {
    this.currentPage = n;
  },

  /**
   * 执行命令消息
   * @param {命令消息} message
   */
  excute(message) {
    var mc = JSON.parse(message.MESSAGE_CONTENT);
    var cmd = mc.COMMAND;
    var sendTime = message.SEND_TIME;
    if (typeof cmd == "undefined") {
      // 如果命令存在
      return;
    }
    // 公共回调
    if (cmd == "excuteParamCommand") {
      mc.PARAM.sendTime = sendTime;
      try {
        // 如果是对所有人公开
        if (mc.PARAM.all) {
          this.excuteParamCommand(mc.PARAM);
          return;
        } else {
          // 如果只对投标人有效
          if (eokb.isBidder()) {
            this.excuteParamCommand(mc.PARAM);
            return;
          }
        }
      } catch (e) {}
      return;
    }

    console.log("命令：" + cmd);
    cmd = "return this." + cmd + "(param)";
    console.log("目标命令：" + cmd);
    var param = mc.PARAM;
    try {
      var func = new Function("param", cmd);
      console.log(func);
      func.call(this, param);
    } catch (e) {
      console.log(e);
      //   alert(e)
    }
  },

  /**
   * 执行参数指令
   * @param {参数} param
   */
  excuteParamCommand(param) {
    var funcName = param.fun;
    // alert(funcName)
    // 如果是刷新公告
    if (funcName == "refreshNotices") {
      // 提示音
      eokb.beep();
      this.refreshNotice();
      return;
    }
    // 流程节点完成
    if (funcName == "finishFlowNodeCommand") {
      // 提示音
      eokb.beep();
      this.finishFlowNodeCommand(param);
      return;
    }

    // 流程节点完成
    if (funcName == "modifyCurrentNodeInfoCommand") {
      // 提示音
      eokb.beep();
      this.modifyCurrentNodeInfoCommand(param);
      return;
    }

    // 结束流程
    if (funcName == "finishTenderProjectFlowCommand") {
      // 提示音
      eokb.beep();
      this.finishTenderProjectFlowCommand();
      return;
    }

    // 刷新流程页面
    if (funcName == "flushFlowNodePage") {
      // 提示音
      eokb.beep();
      var currentNode = param.currentNode;
      this.flushFlowNodePage(currentNode);
      return;
    }
  },

  /**
   * 开标流程结束
   */
  finishTenderProjectFlowCommand() {
    okapp.alert({ message: "开标已结束!" });
    // alert('这里还要刷新流程页面')
    if (this.currentPage == this.FLOW_PAGE) {
      this.flushFlowPage();
    }
  },

  /**
   * 刷新当前节点
   * @param {当前节点} currentNode
   */
  flushFlowNodePage(currentNode) {
    this.$router.push("/turningPage");
  },
  /**
   * 结束流程节点
   * @param {参数} param
   */
  finishFlowNodeCommand(param) {
    // alert('结束的流程名称' + currentNodeName)
    // 当前节点名称
    var currentNodeName = param.currentNodeName;
    // 发送时间
    var time = eokb.formatTimeStamp(param.sendTime);
    // 下一个节点的ID
    var nextNodeID = param.nextNode.ID;
    // 下一个节点名称
    var nextNodeName = param.nextNode.V_NODE_NAME;
    // 下一个节点路径
    var nextNodePath = param.nextNode.V_MOBILE_BIDDER_PAGE_URL;
    // alert('下一个流程节点' + JSON.stringify(param))
    var tipContent = '<div style="padding: 5px;font-size: 16px;"><div>[' + currentNodeName + "]环节已于[" + time + "]结束!</div><div>请各投标人进入[" + nextNodeName + "]环节!</div></div>";
    if (this.mainPage != null && typeof this.mainPage != "undefined") {
      var self = this;
      this.mainPage.$vux.confirm.show({
        title: "流程提示",
        content: tipContent,
        confirmText: "马上进入",
        cancelText: "朕知道了",
        // 组件除show外的属性
        onCancel() {
          // 取消就取消了
          if (self.currentPage == self.FLOW_PAGE) {
            self.flushFlowPage();
          }
        },
        onConfirm() {
          // alert(nextNodePath)
          // 这里跳转到指定的路由页面
          if (nextNodePath != null && typeof nextNodePath != "undefined") {
            // self.mainPage.$router.push(nextNodePath)
            self.mainPage.$router.push({
              path: nextNodePath,
              query: {
                nid: nextNodeID,
                nodeName: nextNodeName
              }
            });
          }
        }
      });
    }

    // 如果当前页是流程页,刷新流程
  },

  /**
   * 修改节点数据
   * @param {参数} param
   */
  modifyCurrentNodeInfoCommand(param) {
    // 提示信息
    var tipMsg = param.tipMsg;
    // 发送时间
    var time = eokb.formatTimeStamp(param.sendTime);
    // 当前节点的ID
    var currentNodeID = param.currentNode.ID;
    // 当前节点的ID
    var nodeStatus = param.currentNode.N_STATUS;
    // 当前节点名称
    var currentNodeName = param.currentNode.V_NODE_NAME;
    // 当前节点路径
    var currentNodePath = param.currentNode.V_MOBILE_BIDDER_PAGE_URL;
    // alert('下一个流程节点' + JSON.stringify(param))
    var tipContent = '<div style="padding: 5px;font-size: 16px;"><div>推送时间：' + time + "</br>[" + tipMsg + "!]</div></div>";
    if (this.mainPage != null && typeof this.mainPage != "undefined") {
      var self = this;
      this.mainPage.$vux.confirm.show({
        title: "流程提示",
        content: tipContent,
        confirmText: "马上进入",
        cancelText: "朕知道了",
        // 组件除show外的属性
        onCancel() {
          // 取消就取消了
          if (self.currentPage == self.OTHER_PAGE) {
            self.flushFlowPage();
          }
        },
        onConfirm() {
          // 路由如果相同不会再次请求，如果当前页面的路由和本节点路由一致，直接刷新页面
          if (self.mainPage.$route.path == currentNodePath) {
            self.flushFlowPage();
            return;
          }
          // 这里跳转到指定的路由页面
          if (currentNodePath != null && typeof currentNodePath != "undefined") {
            // self.mainPage.$router.push(nextNodePath)

            self.mainPage.$router.push({
              path: currentNodePath,
              query: {
                nid: currentNodeID,
                nodeName: currentNodeName,
                nodeStatus: nodeStatus
              }
            });
          }
        }
      });
    }
  },

  /**
   * 刷新流程页
   */
  flushFlowPage() {
    this.mainPage.$router.push("/turningPage");
  },

  /**
   * 增加在线会议参与者
   * @param {参数} param
   */
  addParticipant(param) {
    this.getMeetingRoomParticipants();
  },
  /**
   * 删除在线会议参与者
   * @param {*} param
   */
  removeParticipant(param) {
    this.getMeetingRoomParticipants();
  },
  /**
   * 获取会议室参会人员列表
   */
  getMeetingRoomParticipants() {
    // 如果当前页是人员列表页面
    if (this.currentPage == this.PARTICIPANT_PAGE) {
      this.participantPage.getParticipants();
      this.participantPage.hintOnline();
      return;
    }
    // 如果在其他页面
    this.mainPage.hintOnline();
    // 当前页是消息页面
    if (this.currentPage == this.MESSAGE_PAGE) {
      this.messagePage.getMeetingRoomTenderer();
      return;
    }
  },

  /**
   * 刷新公告
   */
  refreshNotice() {
    // 当前页是公告页面
    if (this.currentPage == this.NOTICE_PAGE) {
      this.noticePage.getTPNotices();
      return;
    }
    this.noticeCount++;
    // 如果在其他页面
    this.mainPage.noticeCount = this.noticeCount;
  },
  /**
   * 刷新聊天消息
   * @param {消息} msg
   */
  refreshMessage(msg) {
    // 当前页是消息页面
    if (this.currentPage == this.MESSAGE_PAGE) {
      this.messagePage.receiveMsg(msg);
      return;
    }
    // 如果在其他页面
    this.msgs.push(msg);
    // 更新一下Main的小红标
    this.mainPage.msgCount = this.msgs.length;
    // 提示音
    this.mainPage.hintMsg();
    // IM Page存在
    if (this.imPage != null && typeof this.imPage != "undefined") {
      this.imPage.unreadMsgCount = this.msgs.length;
    }
  },

  /**
   * 出栈未读消息
   */
  popUnreadMessage() {
    // 未读消息
    var urMsgs = this.msgs;
    this.msgs = [];
    // 更新一下Main的小红标
    this.mainPage.msgCount = "";
    // IM Page存在
    if (this.imPage != null && typeof this.imPage != "undefined") {
      this.imPage.unreadMsgCount = "";
    }
    return urMsgs;
  },

  /**
   * 清空Main.vue中的通知提示数量
   */
  clearMainNoticeCount() {
    this.noticeCount = 0;
    this.mainPage.noticeCount = "";
  }
};

// 导出对象，然后在全局注册或在单个vue文件下调用
export default command;



