// @ == src
import session from '@/js/session.js'
const eokb = {

  /**
   * 获取APP ID
   */
  getAppID() {
    return '8941e906d0804e459bc9c6a67d73437a'
  },

  /**
   * 获取APP密钥
   */
  getAppSecretKey() {
    return 'qw32sE'
  },

  /**
   * 获取客户端类型
   */
  getClientType() {
    return 'EOKB-MOBILE'
  },

  /**
   * 设置是否为安静模式
   * @param {是否静默} bool 
   */
  setMute(bool) {
    console.log(bool)
    session.setValue('EOKB-Mute', bool)
  },

  /**
   * 获取当前设置是否为静默
   */
  getMute() {
    let v = session.getValue('EOKB-Mute')
    // 如果值不存在
    if (null == v || typeof (v) == 'undefined') {
      return false
    }
    return v
  },

  /**
   * 峰鸣声
   */
  beep() {
    // 如果不是安静模式
    if (!this.getMute()) {
      okapp.beep(1)
    }
  },

  /**
   * 设置服务基础路径
   * @param {服务基础路径} url 
   */
  setServiceBaseURL(url) {
    console.log(url)
    session.setValue('ServiceBaseURL', url)
  },

  /**
   * 获取服务基础路径
   */
  getServiceBaseURL() {
    let v = session.getValue('ServiceBaseURL')
    console.log(v)
    return 'http://192.168.1.53:8080/eokb'
  },

  /**
   * 获取服务器端手机的访问根路径
   */
  getMobileAppHome() {
    // 获取基础路径
    let baseURL = this.getServiceBaseURL()
    return baseURL + '/mobile-app'
  },

  /**
   * 
   * @param {WebSocket协议地址} url 
   */
  setWebSocketURL(url) {
    console.log(url)
    session.setValue('WebSocketURL', url)
  },

  /**
   * 返回WebSocket协议地址
   */
  getWebSocketURL() {
    let v = session.getValue('WebSocketURL')
    console.log(v)
    v = v + '/bomim' + '?token=' + this.getAuthzToken() + '&tpid=' + this.getTenderProjectID() + '&utype=' + this.getUserType() + '&ct=' + this.getClientType()
    console.log(v)
    return v
  },

  /**
   * 获取开标服务器地址
   */
  getRegionRoomURL() {
    return this.getServiceBaseURL() + '/authorize'
  },

  /**
   * 设置选择的招标项目ID
   * @param {招标项目ID} id 
   */
  setTenderProjectID(id) {
    session.setValue('TenderProjectID', id)
    console.log(id)
  },

  /**
   * 获取选择的招标项目ID
   */
  getTenderProjectID() {
    let v = session.getValue('TenderProjectID')
    // 如果值不存在
    if (null == v || typeof (v) == 'undefined') {
      return ''
    }
    return v
  },

  /**
   * 设置招标项目信息
   * @param {招标项目信息} tpInfo 
   */
  setTenderProjectInfo(tpInfo) {
    session.setObject('TenderProjectInfo', tpInfo)
  },

  /**
   * 获取招标项目信息
   */
  getTenderProjectInfo() {
    var u = session.getObject('TenderProjectInfo')
    console.log(u)
    return u
  },

  /**
   * 设置用户类型
   * @param {用户类型} type 
   */
  setUserType(type) {
    session.setValue('UserType', type)
    console.log(type)
  },

  /**
   * 获取用户类型
   */
  getUserType() {
    let v = session.getValue('UserType')
    console.log(v)
    // 如果值不存在
    if (null == v || typeof (v) == 'undefined') {
      return '1'
    }
    return v
  },

  /**
   * 判断当前用户是否为投标人
   */
  isBidder() {
    return this.getUserType() == '1'
  },

  /**
   * 设置Token值
   * @param {认证Token} token 
   */
  setAuthzToken(token) {
    session.setValue('AuthzToken', token)
    console.log(token)
  },

  /**
   * 获取Token值
   */
  getAuthzToken() {
    let v = session.getValue('AuthzToken')
    console.log(v)
    // 如果值不存在
    if (null == v || typeof (v) == 'undefined') {
      return ''
    }
    return v
  },

  /**
   * 获取Http请求头TOKEN
   */
  getHttpRequestAuthzToken() {
    let t = this.getAuthzToken()
    if (null == t || typeof (t) == 'undefined' || t == '') {
      return ''
    }
    return 'EOKB ' + this.getAuthzToken()
  },

  /**
   * 设置当前用户信息
   * @param {用户信息} user 
   */
  setCurrentUserInfo(user) {
    session.setObject('CurrentUserInfo', user)
  },

  /**
   * 获取当前用户信息
   */
  getCurrentUserInfo() {
    var userinfo = session.getObject('CurrentUserInfo')
    console.log(userinfo)
    return userinfo
  },

  /**
   * 获取当前用户的ID
   */
  getCurrentUserID() {
    return this.getCurrentUserInfo().USER_ID
  // return '539d39fc42ce30fe9be3bc4b1675cd96'
  },

  /**
   * 设置OKApp的用户信息
   * @param {OKApp的用户信息} user 
   */
  setOKAppUser(user) {
    session.setObject('CurrentOKAppUser', user)
  },

  /**
   * 获取OKApp的用户信息
   */
  getOKAppUser() {
    var u = session.getObject('CurrentOKAppUser')
    console.log(u)
    return u
  },

  /**
   * 增加摄像头
   * @param {摄像头名称} name 
   * @param {HLS协议路径} hls 
   */
  addVideo(name, hls) {
    // 先获取列表
    var videos = this.getVideos()
    // 构建一个对象
    var video = {name: name,src: hls}
    videos.push(video)
    session.setObject('EOKB-VIDEOS', videos)
  },

  /**
   * 获取所有摄像头列表
   */
  getVideos() {
    // 获取开标摄像头列表
    var vs = session.getObject('EOKB-VIDEOS')
    // 如果不存在值,非数组
    if (null == vs || typeof (vs) == 'undefined' || !Array.isArray(vs)) {
      // return []
      // 测试数据
      let rs = [
        {name: '开标室4-01',src: 'http://171.8.242.118/m3u8/show1/rtsp401.m3u8'},
        {name: '开标室4摇球机',src: 'http://171.8.242.118/m3u8/show1/rtsp402.m3u8'},
        {name: '开标室5-01',src: 'http://171.8.242.118/m3u8/show1/rtsp501.m3u8'},
        {name: '开标室5摇球机',src: 'http://171.8.242.118/m3u8/show1/rtsp502.m3u8'},
        {name: '开标室8-01',src: 'http://171.8.242.118/m3u8/show1/rtsp801.m3u8'},
        {name: '开标室8摇球机',src: 'http://171.8.242.118/m3u8/show1/rtsp802.m3u8'}
      ]
      return rs
    }
    return vs
  },

  /**
   * 格式化时间戳
   * @param {时间戳} timestamp 
   */
  formatTimeStamp(timestamp) {
    return this.dateFormat(new Date(timestamp), 'yyyy-MM-dd hh:mm:ss')
  },
  /**
   * 格式化时间
   * @param {日期} date 
   * @param {格式yyyy-MM-dd hh:mm:ss} fmt 
   */
  dateFormat(date, fmt) {
    // author: meizz
    var o = {
      'M+': date.getMonth() + 1, // 月份
      'd+': date.getDate(), // 日
      'h+': date.getHours(), // 小时
      'm+': date.getMinutes(), // 分
      's+': date.getSeconds(), // 秒
      'q+': Math.floor((date.getMonth() + 3) / 3), // 季度
      S: date.getMilliseconds() // 毫秒
    }
    if (/(y+)/.test(fmt))
      fmt = fmt.replace(
        RegExp.$1,
        (date.getFullYear() + '').substr(4 - RegExp.$1.length)
    )
    for (var k in o)
      if (new RegExp('(' + k + ')').test(fmt))
        fmt = fmt.replace(
          RegExp.$1,
          RegExp.$1.length == 1
            ? o[k]
            : ('00' + o[k]).substr(('' + o[k]).length)
    )
    return fmt
  }
}

// 导出对象，然后在全局注册或在单个vue文件下调用
export default eokb



