const session = {

  /**
   * 设置键值对,注意值只能是数字或者字符串
   * @param {键} key 
   * @param {值} value 
   */
  setValue(key, value) {
    window.sessionStorage.setItem(
      key,
      value
    )
  },

  /**
   * 设置键值对,注意值可以是任意对象
   * @param {键} key 
   * @param {值} value 
   */
  setObject(key, value) {
    this.setValue(key, JSON.stringify(value))
  },

  /**
   * 获取键值,注意该值取到的就是字符串或者数字
   * @param {键} key 
   */
  getValue(key) {
    var v = window.sessionStorage.getItem(key)
    return v
  },

  /**
   * 获取键值,注意该值取到的可以是任意对象
   * @param {键} key 
   */
  getObject(key) {
    var v = this.getValue(key)
    // 如果值不存在
    if (null == v || typeof (v) == 'undefined') {
      return {}
    }
    return JSON.parse(v)
  },

  /**
   * 清空所有的缓存内容
   */
  clear() {
    // 从sessionStorage删除所有保存的数据
    window.sessionStorage.clear()
  }

}

// 导出对象，然后在全局注册或在单个vue文件下调用
export default session



